// log
import store from "../store";

export function getLanguageData(payload) {
    return {
      type: "GET_LANGUAGE_DATA",
      payload: payload
    };
  }


export function userAuth(payload) {
    return {
      type: "USER_AUTHENTICATION",
      payload: payload
    };
  }


export function userImg(payload) {
    return {
      type: "USER_IMAGE",
      payload: payload
    };
  }
