//src/components/NFTCard.js
import React from 'react';
import '../../styles/picnic.css';
import axios from "axios";
import dotenv from 'dotenv';


//component that takes an nft object and maps it to corresponding elements
const NFTCard = ({nft}) => {

    const imageClick = async (image, ctAddress) => {

        const loggedInUser = localStorage.getItem("email");
        if (loggedInUser) {
          updateImage(loggedInUser, image, ctAddress);
        }else{
            alert("Please try again");
        }
    };

      const updateImage = async (email, image, ctAddress) => {
          const userData = { email: email, imagePath:image, ctAddress:ctAddress};
          const API_URL = process.env.REACT_APP_API_URL + "/updateImage/";
          axios.post(API_URL, userData).then(response => {
            if (response.data == 'success') {
                    alert("Profile picture updated.")
                    window.location.reload(false);
                }else {
                    alert("Please try again");
                }
            // if(response.data) { setResMessage('User added successfuly.'); }
            //   else { setResMessage(response.data) } 
          }  ).catch(error => {
                  console.error('There was an error!', error);
              });
          // if(checkEmailResponse.data){ return true;  } else {return false;}
      };

    return (
            <img src={nft.image} alt="" className='imageThumb max-w-lg overflow-hidden shadow-lg px-2 py-2' onClick={()=>imageClick(nft.image, nft.ctAddress)}/>
    )
}

export default NFTCard;