import React, { useEffect, useState, Component } from "react";
import axios from "axios";
import dotenv from 'dotenv';
import '../../styles/picnic.css';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import Alert from 'react-bootstrap/Alert';
import {useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { userAuth } from "../../redux/languageData/languageActions";

// import * as s from "../../styles/globalStyles";


function Login() {

    const dispatch = useDispatch();
    const languageData = useSelector((state) => state.languageData);

    const [language, setLanguage] = useState({});
    const [lang, setLang] = useState(languageData.languageName);

    const [passwordType, setPasswordType] = useState("password");
    const [passwordInput, setPasswordInput] = useState("");
    const [email,setEmail] = useState('');
    const [alertType, setAlertType] = useState(false);
    const [resMessage, setResMessage] = useState(false);
    const [terms, setTerms] = useState(false);

    const navigate = useNavigate();


    useEffect(() => {
      if(languageData.languageName != lang) { switchLanguage(languageData.languageName); setLang(languageData.languageName); }
    });

    useEffect(() => {
      switchLanguage(languageData.languageName);
    }, []);

    const switchLanguage = async (languages) => {
      const configResponse = await fetch("/locales/"+ languages +".json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const config = await configResponse.json();
      setLanguage(config);
    };

    const handlePasswordChange =(evnt)=>{
        setPasswordInput(evnt.target.value);
    }
    const togglePassword =()=>{
      if(passwordType==="password")
      {
       setPasswordType("text")
       return;
      }
      setPasswordType("password")
    }

  function isValidEmail(email) {
    return /\S+@\S+\.\S+/.test(email);
  }

  const handleSubmit  = () => {
    setAlertType("danger");
    if(!email)
    {
      setResMessage(language.enter_email_or_password)        ;
      return false;
    }
    else if(!passwordInput)
    {
      setResMessage(language.password_required)        ;
      return false;
    }

    const vEmail = email.trim().toLowerCase();
    if(!isValidEmail(vEmail))
    {
      setResMessage(language.invalid_email);
    }
    if(!terms)
    {
      setResMessage(language.please_accept_terms);
    }else{
      doLogin();
    }
  }


  const doLogin = async () => {
    const userData = { email: email, password:passwordInput};
    const API_URL = process.env.REACT_APP_API_URL + "/login/";
    axios.post(API_URL, userData).then(response => {
      if (typeof response.data === 'string') {
              setResMessage(response.data)
          }else {
            setAlertType("success");
            dispatch(userAuth({"loggedIn":true, "userEmail":response.data.email}));
            localStorage.setItem('email', response.data.email);
            setResMessage(language.account_logged_in);
            navigate("/profile");
            // window.location.reload(true);
          }
    }  ).catch(error => {
            console.error('There was an error!', error);
        });
  };
    return (
      <div className="auth-inner">
      <div className="form-section">
      <div className="inputWithButton">
        <h3 className="wel-center"> {language.welcome_back}</h3>
        <img className="picnic-icon" src={"/config/images/CapturePicnic.png"} />
        </div>
        {resMessage && <Alert variant="danger"> {resMessage}</Alert>}
        <div className="mb-3">
          <label>{language.email}</label>
          <input
            type="email"
            className="form-control"
            id="email"
            value={email}  onChange = {e => setEmail(e.target.value)}
            placeholder={language.enter_email_or_password}
          />
        </div>
        <div className="mb-3">
          <label>{language.password}</label>
          <div className="inputWithButton">
                    <input type={passwordType} onChange={handlePasswordChange} value={passwordInput} name="password" className="form-control pass-input" placeholder={language.password} />                     
                     { passwordType==="password"? <svg width="20" height="17" onClick={togglePassword} fill="currentColor" className="bi bi-eye-slash-fill eye" viewBox="0 0 16 16">
              <path d="m10.79 12.912-1.614-1.615a3.5 3.5 0 0 1-4.474-4.474l-2.06-2.06C.938 6.278 0 8 0 8s3 5.5 8 5.5a7.029 7.029 0 0 0 2.79-.588zM5.21 3.088A7.028 7.028 0 0 1 8 2.5c5 0 8 5.5 8 5.5s-.939 1.721-2.641 3.238l-2.062-2.062a3.5 3.5 0 0 0-4.474-4.474L5.21 3.089z" />
              <path d="M5.525 7.646a2.5 2.5 0 0 0 2.829 2.829l-2.83-2.829zm4.95.708-2.829-2.83a2.5 2.5 0 0 1 2.829 2.829zm3.171 6-12-12 .708-.708 12 12-.708.708z" />
            </svg> :<svg width="20" height="17" fill="currentColor" onClick={togglePassword} className="bi bi-eye-fill eye" viewBox="0 0 16 16" >
              <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z" />
              <path d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z" />
            </svg> }
                
          </div>

        </div>
        <p className="forgot-password text-left forgot-p">
          {language.forgot_your_password}
        </p>
        <div className="mb-3">
          <div className="custom-control custom-checkbox">
            <input
              type="checkbox"
              className="custom-control-input"
              value={terms}  onChange = {e => setTerms(e.target.value)}
              id="customCheck1"
            />
            <label className="custom-control-label" htmlFor="customCheck1">
              &nbsp; {language.i_accept_the} <span className="highlighted-text">{language.term_of_service}</span> and <span className="highlighted-text">{language.privacy_policy}</span>
            </label>
          </div>
        </div>
        <div className="d-grid">
          <button type="button" onClick={()=>handleSubmit()} className="btn btn-primary">
            {language.login}
          </button>
        </div>
      </div>
      <p className="bottom-link">
        {language.not_a_member} <a style={{textDecoration: 'none'}} href="/signup" ><span className="text-decoration: none">{language.sign_up}</span></a> 
      </p>
      </div>
    )
  }


  export default Login;