import React, { useEffect, useState, Component } from "react";
import '../../styles/picnic.css';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import axios from "axios";
import dotenv from 'dotenv';
import {useNavigate } from 'react-router-dom';
import Alert from 'react-bootstrap/Alert';
import { useDispatch, useSelector } from "react-redux";
import { getLanguageData } from "../../redux/languageData/languageActions";
import { userAuth } from "../../redux/languageData/languageActions";


// import * as s from "../../styles/globalStyles";
dotenv.config();

function SignUp() {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const languageData = useSelector((state) => state.languageData);

  const [errMessage, setErrMessage] = useState("");
  const [passMatch, setPassMatch] = useState(false);
  const [isSubmit, setIsSubmit] = useState(false);
  const [resMessage, setResMessage] = useState(false);
  const [ageMessage, setAgeMessage] = useState(false);
  const [passwordErrMsg, setPasswordErrMsg] = useState(false);
  const [language, setLanguage] = useState({});
  const [lang, setLang] = useState(languageData.languageName);

  const [email, setEmail] = React.useState({
    error: false,
    value: ""
  });


    useEffect(() => {
      if(languageData.languageName != lang) { switchLanguage(languageData.languageName); setLang(languageData.languageName);}
    });

useEffect(() => {
  switchLanguage(languageData.languageName);
}, []);

    const switchLanguage = async (languages) => {
      const configResponse = await fetch("/locales/"+ languages +".json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const config = await configResponse.json();
      setLanguage(config);
    };

    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [dob, setDob] = useState();
    const [username, setUsername] = useState('');
    const [password,setPassword] = useState('');
    const [cpassword,setCpassword] = useState('');

    const matchPassword = (e) => {
      (password != e.target.value) ? setPassMatch(true) : setPassMatch(false);
    }

    const checkPassword = (e) => {
      //-- password validation filters --//
      const uppercaseRegExp   = /(?=.*?[A-Z])/;
      const lowercaseRegExp   = /(?=.*?[a-z])/;
      const minLengthRegExp   = /.{6,}/;

      const passwordLength =      password.length;
      const uppercasePassword =   uppercaseRegExp.test(password);
      const lowercasePassword =   lowercaseRegExp.test(password);
      const minLengthPassword =   minLengthRegExp.test(password);

        if(!uppercasePassword || !lowercasePassword || !minLengthPassword){
                setPasswordErrMsg(true);
        }else{
          setPasswordErrMsg(false);
        }
      }

    const handleInputChange = (e) => {
        const {id , value} = e.target;
        if(id === "firstName"){
            setFirstName(value);
        }
        if(id === "lastName"){
            setLastName(value);
        }
        if(id === "username"){
            setUsername(value);
        }
        // if(id === "dob"){
        //     setEmail(value);
        // }
        if(id === "password"){
            setPassword(value);
        }
        if(id === "cpassword"){
            setCpassword(value);
        }

    }

    const handleSubmit  = async () => {
      if(!firstName || !lastName || !dob || !email.value || !password)
      {
        setIsSubmit("danger"); setResMessage(language.fill_all_fileds) 
        return false;
      }if(email.error || ageMessage || passMatch)
      {
        setIsSubmit("danger"); setResMessage(language.fill_all_valid) 
        return false;
      }else{
        // alert('user added');
        await addUser();
      }
    }

  function isValidEmail(email) {
    return /\S+@\S+\.\S+/.test(email);
  }

  const handleChange = async (e: any) => {
    // Trim value & convert to lowercase
    const value = e.target.value.trim().toLowerCase();
    if(!isValidEmail(value))
    {
      email.error = true;
      setErrMessage(language.invalid_email);
      // resetForm();      
    }
    else
    {
      //-- Check is email already exists
      const isEmailExists = await checkEmail(value)//false;//re.test(value);
      setEmail({
        value,
        error: isEmailExists
      });
    }
  };
// localhost:4000/api/user/rishabhbhojak111@gmail.comshine_
  const checkEmail = async (email) => {
      const checkEmailUrl = process.env.REACT_APP_API_URL + "/user/" + email.toLowerCase();
      const checkEmailResponse = await axios.get(checkEmailUrl);
      console.log("checkEmailResponse", checkEmailResponse);
      if(checkEmailResponse.data) {setErrMessage(errMessage => language.try_another_email);}
      return checkEmailResponse.data;
      // if(checkEmailResponse.data){ return true;  } else {return false;}
  };

const  getAge = (dateString)=>{
    var today = new Date();
    var birthDate = new Date(dateString);
    var age = today.getFullYear() - birthDate.getFullYear();
    var m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
    }
    return age;
}

const changeDOB = async (date) => {
  let age = getAge(date);
  if(age < 13 ){ setAgeMessage(true); }else {setAgeMessage(false); setDob(date); }
}

const addUser = async () => {
    // POST request using axios with error handling
    const userData = { firstName: firstName, lastName:lastName, dob:dob, username:username, email:email.value, password:password };
    const API_URL = process.env.REACT_APP_API_URL + "/addUser/";
    axios.post(API_URL, userData).then(response => {
      if(response.data == "success") { setIsSubmit("success"); setResMessage(language.account_added); 
       setTimeout(function() { dispatch(userAuth({"loggedIn":true, "userEmail":email.value})); localStorage.setItem('email', email); navigate("/profile"); window.location.reload(false); }, 4000); resetForm(); }
        else { setIsSubmit("danger"); setResMessage(language.error_try_again) } 
    }  ).catch(error => {
            console.error('There was an error!', error);
        });
}

  const resetForm = async () => {
    setFirstName(''); 
    setLastName(''); 
    setUsername(''); 
    setEmail({error: false, value: ""}); 
    setPassword(''); 
    setCpassword(''); 
  }
    return (
      <div className="auth-inner">
      <form className="form-section">
      <div className="inputWithButton">
        <h3 className="wel-center"> {language.lets_get_started}</h3>
        <img className="picnic-icon" src={"/config/images/CapturePicnic.png"} />
        </div>

        {isSubmit && <Alert variant={isSubmit}> {resMessage}</Alert>  }
        <div className="mb-3">
          <label>{language.first_name}</label>
          <input
            type="text" className="form-control" id="firstName" value={firstName}  onChange = {e => setFirstName(e.target.value)} placeholder={language.first_name}
          />
        </div>
        <div className="mb-3">
          <label>{language.last_name}</label>
          <input type="text" className="form-control" id="lastName" value={lastName}  onChange = {e => setLastName(e.target.value)}  placeholder={language.last_name} />
        </div>
        <div className="mb-3">
          <label>{language.username}</label>
          <input
            type="text"
            className="form-control" id="username" value={username}  onChange = {e => setUsername(e.target.value)}
            placeholder={language.username}
          />
        </div>
        <div className="mb-3">
          <label>{language.dob}</label>
          <input
            type="date"
            className="form-control" id="dob"  
            placeholder="DOB (MMM DD, YYYY)"
            onChange ={e => changeDOB(e.target.value)} //{e => setDob(e.target.value)}
          />
          {ageMessage && <p className="alert alert-danger"> You must be atleast 13 years of old to join.</p>}
        </div>
        <div className="mb-3">
          <label>{language.email}</label>
          <input
            type="email"
            className="form-control"
            placeholder={language.enter_email}
            onBlur={handleChange}
          />
          {email.error && <p className="alert alert-danger"> {errMessage}</p>}
        </div>
        <div className="mb-3">
          <label>{language.password}</label>
          <input
            type="password"
            name="password"
            className="form-control" id="password" value={password}  onChange = {e => setPassword(e.target.value)}
            onBlur={checkPassword}
            placeholder={language.password}
          />
          {passwordErrMsg && <p className="alert alert-danger"> {language.must_be_six} </p>}
        </div>
        <div className="mb-3">
          <label>{language.confirm_password}</label>
          <input
            type="password"
            name="cpassword"
            className="form-control" id="cpassword" value={cpassword}  onChange = {e =>  setCpassword(e.target.value) }
            onBlur={matchPassword}
            placeholder={language.confirm_password}
          />
          {passMatch && <p className="alert alert-danger"> {language.both_pass_same}</p>}
        </div>
        <div className="d-grid">
          <button type="button" onClick={()=>handleSubmit()} className="btn btn-primary">
            {language.sign_up}
          </button>
        </div>
        <p className="forgot-password text-right forgot-p">
          <a style={{textDecoration: 'none'}} href="/login"> <span className="highlighted-text">{language.already_a_member} {language.login}  </span> </a>
        </p>
      </form>
      </div>
    )
  }

export default SignUp;