import React, { useEffect, useState, Component, useMemo } from "react";
import '../../styles/picnic.css';
import Gallery from '../Gallery/Gallery.js';
// import Table from '../Table/Table.js';
import Modal from '../Modal/Modal.js';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import 'react-phone-number-input/style.css'
import axios from "axios";
import dotenv from 'dotenv';
import Alert from 'react-bootstrap/Alert';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../../redux/data/dataActions";
import { userAuth, userImg } from "../../redux/languageData/languageActions";
import Web3 from "web3";
import moment from "moment";

function Wallet() {

    const [firstName, setFirstName] = useState(false);
    const [name, setName] = useState(false);
    const [value, setValue] = useState();
    const [isSubmit, setIsSubmit] = useState(false);
    const [resMessage, setResMessage] = useState(false);
    const [loggedIn, setLoggedIn] = useState(true);
    const [userEmail, setUserEmail] = useState(false);
    
    const [isValidImage, setIsValidImage] = useState(true);

    const dispatch = useDispatch();
    const blockchain = useSelector((state) => state.blockchain);
    const data = useSelector((state) => state.data);
    const languageData = useSelector((state) => state.languageData);

    const [language, setLanguage] = useState({});
    const [lang, setLang] = useState(languageData.languageName);
    const [image, setImage] = useState('');

    const [show, setShow] = useState(false);
    const [showQR, setShowQR] = useState(false);
    const [showSendEther, setShowSendEther] = useState(false);
    const [balance, setBalance] = useState(10.000000);
    const [account, setAccount] = useState(false);
    const [receiverAddress, setReceiverAddress] = useState("");
    const [errMessage, setErrMessage] = useState(false);
    const [varient, setVarient] = useState("danger");
    const [txAmount, setTxAmount] = useState(0);
    const [dataRows, setDataRows] = useState(5);
    const [transactionData, setTransactionData] = useState(false)
    const [loadingTx, setLoadingTx] = useState(true)

    const navigate = useNavigate();

    useEffect(() => {
      // dispatch(connect());
      getData();
    }, [blockchain.account]);

useEffect(() => {
      // dispatch(connect());
      getData();
    }, [blockchain.balance]);

     
    useEffect(() => {
        const loggedInUser = localStorage.getItem("email");
        if (languageData.loggedIn && languageData.userEmail) {
          dispatch(connect());
          setLoggedIn(true);
          setImage(languageData.userImg);
          setName(languageData.userName);
          // getUserData(languageData.userEmail);
          // setUserEmail(languageData.userEmail);
        }
      }, [languageData.loggedIn, languageData.userEmail, dispatch]);

    useEffect(() => {
      if(languageData.languageName != lang) {switchLanguage(languageData.languageName); setLang(languageData.languageName);}
    });

    useEffect(() => {
      switchLanguage(languageData.languageName);
    }, []);

    const getData = async () => {
      if (blockchain.account !== "" && blockchain.smartContract !== null) {
        dispatch(fetchData(blockchain.account));
        setBalance(blockchain.balance);
        await setAccount(blockchain.account);
        // await getTransaction();
        // await fetchInfo();
      }
    };

    const switchLanguage = async (languages) => {
      const configResponse = await fetch("/locales/"+ languages +".json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const config = await configResponse.json();
      setLanguage(config);
    };

    const getUserData = async (email) => {
      const userData = { email: email};
      const API_URL = process.env.REACT_APP_API_URL + "/getUserData/";
      axios.post(API_URL, userData).then(response => {
        if (typeof response.data === 'string') {
                localStorage.removeItem('email');
                navigate("/login");
            }else {
              let name = response.data.firstName+' '+response.data.lastName;
                setFirstName(name.toUpperCase());
                setImage(response.data.image);
            }
      }  ).catch(error => {
              console.error('There was an error!', error);
          });
    };


  const fetchInfo = () => {
    if(account)
    {
        // setAccount();
    setLoadingTx(true);
    return fetch("http://api.etherscan.io/api?module=account&action=txlist&address="+ account +"&startblock=0&endblock=99999999&sort=desc&page=1&offset="+ dataRows +"&apikey=71NB6J7H2UG4H4WP3AT58IQZ86652AI4QD")
      .then((res) => res.json())
      .then((d) => { if(d.result.length > 0) {setTransactionData(d.result); } })
      .finally(() => {
            setLoadingTx(false)
            setDataRows(10);
          })
    }
  }

    const getTransaction = async () => {
        setLoadingTx(true)
        fetch("http://api.etherscan.io/api?module=account&action=txlist&address="+ account +"&startblock=0&endblock=99999999&sort=desc&page=1&offset="+ dataRows +"&apikey=71NB6J7H2UG4H4WP3AT58IQZ86652AI4QD")
          .then(response => response.json())
          .then(json => { if(json.result.length > 0) {setTransactionData(json.result)} })
          .finally(() => {
            setLoadingTx(false)
          })
    }


const loadMore = async () => {
  await setDataRows(dataRows + 5);
  await getTransaction();

}

const initTransaction = async () => {
  if(receiverAddress == '') {
    setErrMessage(language.receiver_add_req);
    return false;
  }else if(txAmount <= 0) {
    setErrMessage(language.tx_amount_gt_zero);
    return false;
  }else{
    setErrMessage(false);
  }
  try{
    let response = await handleSendTransaction(account, receiverAddress, txAmount);
    setVarient("success");
    setErrMessage(language.trans_sucess);
  } catch (err) {
    setVarient("danger");
    setErrMessage(err.message);
    return false;
  }
}

const handleSendTransaction = async (sender, receiver, amount) => {
  const gasPrice = '0x5208';
  const amountHex = (amount * Math.pow(10,18)).toString(16)

  const tx = {
    from: sender,
    to: receiver,
    value: amountHex,
    gas: gasPrice,
  }

  await window.ethereum.request({method: 'eth_sendTransaction', params: [tx]})
}

return (
      <div className="auth-inner">
      <form className="form-section">
        <div className="inputWithButton">
          <h3 className="wel-center"> {language.my_wallet} </h3>
          <img className="picnic-icon" src={"/config/images/CapturePicnic.png"} />
        </div>
        {isSubmit && <Alert variant={isSubmit}> {resMessage}</Alert>  }
        <div className="mb-3 p-img-div">
          <img className="p-img" src={image}/>
          <br/>
          <label><b>{name}</b></label>
        </div>
        
        <div className="mb-3 full-name inputWithButton">
          <label><b>{language.wallet_balance} : </b></label> {blockchain.account && <label className="highlighted-text">  {blockchain.balance.toFixed(4)}  </label>}
          <div className="txBtnDiv">
            <button type="button" className="btn btn-primary" onClick={() => { setShowSendEther(true); setShow(true); setTxAmount(0); setReceiverAddress(""); setErrMessage(false); } }> {language.send_ether} <img src={"/config/images/send-bitcoin.svg"} /> </button>
            <button type="button" className="btn btn-primary receive-btn" onClick={() => {setShowSendEther(false); setShow(true); setTxAmount(0); setReceiverAddress(""); setErrMessage(false); } }> {language.receive_ether} <img  src={"/config/images/receive-bitcoin.svg"} /> </button>
          </div>
        </div>

        {showSendEther ? ( <Modal title={language.send_ether} alertVarient={varient} balanceTag={language.wallet_balance} message={errMessage} onClose={() => setShow(false)} show={show}  balance={balance.toFixed(4)} >
          
          <div className="mb-3">
            <label>{language.wallet_address} : </label> <br/> <input type="text" className="form-control" placeholder={language.wallet_address} value={receiverAddress} onChange = {e => setReceiverAddress(e.target.value)}  /> 
          </div>
          <div className="mb-3">
            <label>Amount : </label>  <br/> <input type="text" className="form-control" placeholder={language.transaction_amount}  value={txAmount} onChange = {e => setTxAmount(e.target.value)}  /> 
          </div>
          <div className="d-grid">
            <button type="button" onClick={()=>initTransaction()} className="btn btn-primary">
              Send
            </button>
          </div>
      </Modal>) : ( <Modal title={language.receive_ether} alertVarient={varient} balanceTag={language.wallet_balance} message={errMessage} onClose={() => setShow(false)} show={show}  balance={balance.toFixed(4)} >
          
          <div className="mb-3">
            <label><b>{language.wallet_address} : </b>  {blockchain.account} </label> 
          </div>
          <div className="mb-3 img-center">
            <img src={"https://chart.googleapis.com/chart?chs=300x300&chld=L|2&cht=qr&chl=ethereum:"+blockchain.account} />
          </div>
          
      </Modal> ) }
      


      {loadingTx ? ( <div>           <div className="mb-3">
            <button type="button" onClick={()=>loadMore()} className="btn btn-primary">
              {language.show_tx_history}
            </button>
          </div></div> ) : (
        <div className="mb-3">
        
          <h4> {language.wallet_tx_history} </h4>
          <table style={{border: "1px solid rgb(0, 0, 0)"}}>
          <tbody>
            <tr>
              <th>From</th>
              <th>To</th>
              <th>Amount</th>
              <th>Date</th>
              <th>View</th>

            </tr>

{transactionData && transactionData.map((dataObj, index) => {
          return (
              <tr key={dataObj.hash} >
                <td>{dataObj.from.substring(0, 16) + "..." + dataObj.from.substring(dataObj.from.length - 4)}</td>
                <td>{dataObj.to.substring(0, 16) + "..." + dataObj.to.substring(dataObj.to.length - 4)}</td>
                <td>{Web3.utils.fromWei(dataObj.value, 'ether')} ETH</td>
                <td>{moment.unix(dataObj.timeStamp).format("DD/MM/YYYY")}</td>
                <td onClick={(e) => { window.open("https://etherscan.io/tx/"+dataObj.hash, "_blank")}} > <svg width="20"   height="17" fill="#22C47A" className="bi bi-eye-slash-fill" viewBox="0 0 16 16">
              <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z" />
              <path d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z" />
            </svg> </td>

              </tr>
          );
        })}
        </tbody>
          </table>
          <div className="mb-3 load-more-btn">
            <button type="button" onClick={()=>loadMore()} className="btn btn-primary">
              {language.load_more}
            </button>        
          </div>
          </div>
      )}
      </form>
      </div>
    )
  
}

export default Wallet;