import React, { useEffect, useState, Component } from "react";
import '../../styles/picnic.css';
import Gallery from '../Gallery/Gallery.js';
import '../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import 'react-phone-number-input/style.css'
import PhoneInput from 'react-phone-number-input';
import flags from 'react-phone-number-input/flags';
import axios from "axios";
import dotenv from 'dotenv';
import Alert from 'react-bootstrap/Alert';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../../redux/data/dataActions";
import { userImg } from "../../redux/languageData/languageActions";


function Profile() {


    const dispatch = useDispatch();
    const blockchain = useSelector((state) => state.blockchain);
    const data = useSelector((state) => state.data);
    const languageData = useSelector((state) => state.languageData);

    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [dob, setDob] = useState();
    const [email, setEmail] = useState();
    const [userName, setUserName] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');
    const [address, setAddress] = useState('');
    const [bio, setBio] = useState('');
    const [image, setImage] = useState('');
    const [value, setValue] = useState();
    const [virtualData, setVirtualData] = useState(false);
    const [isSubmit, setIsSubmit] = useState(false);
    const [resMessage, setResMessage] = useState(false);
    const [ageMessage, setAgeMessage] = useState(false);
    const [loggedIn, setLoggedIn] = useState(false);
    const [userEmail, setUserEmail] = useState(false);
    const [account, setAccount] = useState(false);

    const [profilePic, setProfilePic] = useState(false);
    const [isValidImage, setIsValidImage] = useState(false);


    const [language, setLanguage] = useState({});
    const [lang, setLang] = useState(languageData.languageName);


    const navigate = useNavigate();

    useEffect(() => {
      // preventDefault();
      // dispatch(connect());
      getData(); 
      console.log("Account Change");
    }, [blockchain.account]);

     
    useEffect(() => {
        const loggedInUser = localStorage.getItem("email");
        if (languageData.loggedIn) {
          setLoggedIn(true);
          setUserEmail(languageData.userEmail);
          setImage(languageData.userImg);
          getUserData(languageData.userEmail);
        }
      }, [languageData.loggedIn, languageData.userEmail, dispatch]);

    useEffect(() => {
      if(languageData.languageName != lang) {switchLanguage(languageData.languageName); setLang(languageData.languageName); }
    });

    useEffect(() => {
      switchLanguage(languageData.languageName);
    }, []);

    useEffect(() => {
          if(blockchain.errorMsg !== "")
          {
            setIsSubmit("danger"); setResMessage(blockchain.errorMsg);
          }else{
            setIsSubmit(false); setResMessage(false);
          }
        }, [blockchain.errorMsg]);


    const getData = async () => {
      if (blockchain.account !== "" && blockchain.smartContract !== null) {
        dispatch(fetchData(blockchain.account));
        await setAccount(blockchain.account);
      }
    };

    const switchLanguage = async (languages) => {
      const configResponse = await fetch("/locales/"+ languages +".json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const config = await configResponse.json();
      setLanguage(config);
    };

    const getUserData = async (email) => {
      const userData = { email: email};
      const API_URL = process.env.REACT_APP_API_URL + "/getUserData/";
      axios.post(API_URL, userData).then(response => {
        if (typeof response.data === 'string') {
                localStorage.removeItem('email');
                navigate("/login");
            }else {
                setFirstName(response.data.firstName);
                setLastName(response.data.lastName);
                setDob(response.data.DOB);
                setUserName(response.data.userName);
                setEmail(response.data.email);
                setValue(response.data.phoneNumber);
                setImage(response.data.image);
                setAddress(response.data.address);
                setIsValidImage(response.data.isPicVerified);
                setBio(response.data.bio);
                if (response.data.image.trim().length != 0) 
                {
                  dispatch(userImg({"userImg":response.data.image, "userName":response.data.firstName+' '+response.data.lastName}));
                }
            }
      }  ).catch(error => {
              console.error('There was an error!', error);
          });
      // if(checkEmailResponse.data){ return true;  } else {return false;}
    };

    const toggleProfilPic = (e)=>{
      if(profilePic === true)
      {
       setProfilePic(false)
       return;
      }else {
          if(account) { setProfilePic(true)
            } else {
              connectWallet(e);
            }
          
      }
    }

const  getAge = (dateString)=>{
    var today = new Date();
    var birthDate = new Date(dateString);
    var age = today.getFullYear() - birthDate.getFullYear();
    var m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
        age--;
    }
    return age;
}

const changeDOB = async (date) => {
  let age = getAge(date);
  if(age < 13 ){ setAgeMessage(true); }else {setAgeMessage(false); setDob(date); }
}

const connectWallet = async (e) => {
  e.preventDefault();
  dispatch(connect());
  getData(); 
  if(account) { navigate("/wallet"); }
}


const updateUser = async () => {
    // POST request using axios with error handling
    const userData = { firstName: firstName, lastName:lastName, DOB:dob, userName:userName, email:email, address:address, bio:bio, phoneNumber:value };
    const API_URL = process.env.REACT_APP_API_URL + "/updateUser/";
    axios.post(API_URL, userData).then(response => {
      
      
      if(response.data.code == 1) { setIsSubmit("success"); setResMessage(language.account_updated); 
        } else if(response.data.code == 2) { setIsSubmit("danger"); setResMessage(language.error_try_again) 
        } else if(response.data.code == 3) { localStorage.removeItem('email'); navigate("/login"); }
    }  ).catch(error => {
            console.error('There was an error!', error);
        });
}

return (
      <div className="auth-inner">
      <form className="form-section">
        <div className="inputWithButton">
          <h3 className="wel-center"> {language.edit_profile} </h3>
          <img className="picnic-icon" src={"/config/images/CapturePicnic.png"} />
        </div>
        {isSubmit && <Alert variant={isSubmit}> {resMessage}</Alert>  }
        <div className="mb-3 p-img-div">
          {isValidImage && <img className="img-valid" src={"/config/images/right-icon5.png"} />}
          <img className="p-img" src={image}/>
          <img className="img-camara cursor-pointer" src={"/config/images/camera-solid.svg"} onClick={(e) => { toggleProfilPic(e) }}/>
        </div>
        {profilePic && <div className="gallery-model"> <Gallery email={userEmail} address={account} /> </div> }
        
        
        
        <div className="mb-3 full-name">
          <input
            type="text"
            className="form-control f-name"
            placeholder={language.first_name}
            value={firstName}  onChange = {e => setFirstName(e.target.value)}
          />
          <input type="text" className="form-control l-name" placeholder={language.last_name} value={lastName}  onChange = {e => setLastName(e.target.value)} />
        </div>
        <div className="mb-3 tooltip">
          <input
            type="text"
            className="form-control"
            placeholder="User name"
            value={userName}  onChange = {e => setUserName(e.target.value)}
            disabled
          />
          <span className="tooltiptext">{language.contact_support_username}</span>
        </div>
        <div className="mb-3">
          <button type="button" onClick={(e) => {connectWallet(e) }} className="form-control wc"> {language.my_crypto_wallet} {blockchain.account && <span className="highlighted-text"> - Connected </span>} </button>
        </div>
        <div className="mb-3 tooltip">
            <input
              type="email"
              className="form-control"
              placeholder={language.enter_email_or_password}
              value={email}
              disabled
            />
            <span className="tooltiptext">{language.contact_support_email}</span>
        </div>
        <div className="mb-3 tooltip">
          
          <input
            type="date"
            className="form-control"
            value={dob}
            placeholder="DOB (MMM DD, YYYY)"
            onChange ={e => changeDOB(e.target.value)}
            disabled
          />
          <span className="tooltiptext">{language.contact_support_dob}</span>
          {ageMessage && <p className="alert alert-danger"> {language.thirteen_year_old}</p>}
        </div>

        <div className="mb-3 inputWithButton">
          <input
            type="text"
            className="form-control"
            placeholder={language.location}
            value={address}  onChange = {e => setAddress(e.target.value)}
          />
          <img className="target-icon" src={"/config/images/icons8-accuracy-50.png"} />
        </div>
        <div className="mb-3">
            <PhoneInput placeholder={language.phone_including_country}  flags={flags} value={value} onChange={setValue}/>
        </div>

        <div className="mb-3">
        <textarea className="form-control" placeholder= {language.provide_bio} value={bio}  onChange = {e => setBio(e.target.value)} ></textarea>
        </div>
        <div className="d-grid">
          <button type="button" onClick={()=>updateUser()} className="btn btn-primary">
            {language.save}
          </button>
        </div>
      </form>
      </div>
    )
  
}

export default Profile;