import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../../redux/data/dataActions";
import { getLanguageData } from "../../redux/languageData/languageActions";
import * as s from "../../styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;


function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const languageData = useSelector((state) => state.languageData);  
  const [claimingNft, setClaimingNft] = useState(false);
  const [language, setLanguage] = useState({});
  const [lang, setLang] = useState(languageData.languageName);
  const [feedback, setFeedback] = useState(language.click_buy_ins);
  const [mintAmount, setMintAmount] = useState(1);
  var etaGesFee = .007070;
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });



    useEffect(() => {
      if(languageData.languageName != lang) {  switchLanguage(languageData.languageName); setLang(languageData.languageName); }
    });

    useEffect(() => {
      switchLanguage(languageData.languageName);
      // setFeedback(language.click_buy_ins);
    }, []);

    const switchLanguage = async (languages) => {
      const configResponse = await fetch("/locales/"+ languages +".json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const config = await configResponse.json();
      setFeedback(config.click_buy_ins);
      setLanguage(config);
    };


    const claimNFTs = () => {
      let cost = CONFIG.WEI_COST;
      let ethCost = CONFIG.ETH_COST;
      let gasLimit = CONFIG.GAS_LIMIT;
      let totalCostWei = String(cost * mintAmount);
      let totalGasLimit = String(gasLimit * mintAmount);
      setFeedback(`${CONFIG.NFT_NAME} Minting Price is ${ethCost * mintAmount}`);
      setClaimingNft(true);
      blockchain.smartContract.methods
        .buyBatch(mintAmount)
        .send({
          gasLimit: String(totalGasLimit),
          to: CONFIG.CONTRACT_ADDRESS,
          from: blockchain.account,
          value: totalCostWei,
        })
        .once("error", (err) => {
          console.log(err);
          setFeedback(language.error_try_again);
          setClaimingNft(false);
        })
        .then((receipt) => {
          console.log(receipt);
          setFeedback(
            `{language.success_the} ${CONFIG.NFT_NAME} {language.is_yours}`
          );
          setClaimingNft(false);
          dispatch(fetchData(blockchain.account));
        });
    };

    const decrementMintAmount = () => {
      let newMintAmount = mintAmount - 1;
      if (newMintAmount < 1) {
        newMintAmount = 1;
      }
      setMintAmount(newMintAmount);
    };

    const incrementMintAmount = () => {
      let newMintAmount = mintAmount + 1;
        if (newMintAmount > 50) {
          newMintAmount = 50;
        }
      setMintAmount(newMintAmount);
    };

    const getData = () => {
      if (blockchain.account !== "" && blockchain.smartContract !== null) {
        dispatch(fetchData(blockchain.account));
      }
    };

    const getConfig = async () => {
      const configResponse = await fetch("/config/config.json", {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });
      const config = await configResponse.json();
      SET_CONFIG(config);
    };

    useEffect(() => {
      getConfig();
    }, []);

    useEffect(() => {
      getData();
    }, [blockchain.account]);

  return (
    <s.Screen>
    <s.SpacerSmall />
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: 24, backgroundColor: "var(--primary)" }}
        image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null}
      >
        <s.SpacerSmall />
        <s.ResponsiveWrapper flex={1} style={{ padding: 24 }} test>
          <s.Container flex={1} jc={"center"} ai={"center"}>
            { languageData.languageName == 'en' ? <img src={"/config/images/PICNIC_APICNICISALWAYS.svg"} /> : <img className="img-spanish" src={"/config/images/home-spanish2.png"} />}
            
          </s.Container>
          <s.SpacerLarge />

          <s.Container
            flex={1}
            jc={"center"}
            ai={"center"}
            style={{
              // backgroundColor: "var(--accent)",
              padding: 24,
              borderRadius: 24,
              // border: "4px dashed rgb(56, 232, 203)",
              boxShadow: "0px 5px 11px 2px var(--accent)",
              // boxShadow: "0px 5px 11px 2px rgba(148,226,204,0.7)",
            }}
          >
            <h3 style={{color: "var(--accent-text)"}}>{language.welcom_title}</h3>

            <s.TextTitle style={{ textAlign: "center", fontSize: 50, fontWeight: "bold", color: "var(--accent-text)", marginBottom: "auto", }} >
               {CONFIG.MAX_SUPPLY - data.totalSupply}
            </s.TextTitle>

            <s.TextDescription style={{ textAlign: "center", color: "var(--primary-text)", fontSize: "22px", }} >
             {language.nft_note} 
            </s.TextDescription>

            <span style={{ textAlign: "center", maxWidth: "180px",}} className ="tooltip">             
              <s.StyledButton style={{ fontSize: "16px", }} onClick={(e) => { window.open(CONFIG.MARKETPLACE_LINK, "_blank");}} >
                VIEW
              </s.StyledButton>
              <span className="tooltiptext">Connect to OpenSea?</span>
            </span>
            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  {language.end_sale_note}
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <s.StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </s.StyledLink>
              </>
            ) : (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--primary-text)" }}
                >
                {language.each} {CONFIG.SYMBOL} {language.is_only} {CONFIG.DISPLAY_COST}{" "}
                  {CONFIG.NETWORK.SYMBOL} <br/> {language.buy_before}
                </s.TextTitle>
                <s.SpacerXSmall />
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  {language.exclude_fee}
                </s.TextDescription>
                <s.SpacerSmall />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {language.connect_to_the} {CONFIG.NETWORK.NAME} {language.network}
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <s.StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      {language.connect_btn}
                    </s.StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--accent-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>
                    <s.SpacerMedium />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <s.StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </s.StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent-text)",
                        }}
                      >
                        {mintAmount}
                      </s.TextDescription>
                      <s.SpacerMedium />
                      <s.StyledRoundButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </s.StyledRoundButton>
                    </s.Container>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <s.StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? languageData.languageName == 'en' ? "BUSY" : "Trabajo" :  languageData.languageName == 'en' ? "Buy" : "Comprar" }
                      </s.StyledButton>
                    </s.Container>
                  </>
                )}
              </>
            )}
            <s.SpacerMedium />
          </s.Container>
          
          <s.SpacerLarge />

        </s.ResponsiveWrapper>
        <s.SpacerMedium />
        <s.Container jc={"center"} ai={"center"} style={{ width: "70%" }}>
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
            {language.note1} (
            {CONFIG.NETWORK.NAME}) {language.note2}
          </s.TextDescription>
          
          <s.TextDescription
            style={{
              textAlign: "center",
              color: "var(--primary-text)",
            }}
          >
            {language.gas_limit_note1} {CONFIG.GAS_LIMIT} {language.gas_limit_note2}
          </s.TextDescription>
        </s.Container>
      </s.Container>
    </s.Screen>
  );
}

export default App;
